@tailwind base;
@tailwind components;
@tailwind utilities;

*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
::-webkit-scrollbar
{
  display: none;
}
:root
{
  --sideNav-width:200px;
}
.sideNav{
  width: var(--sideNav-width);
  min-height: -webkit-fill-available;
}

